body {
  margin: 0;
  padding: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

a {
  text-decoration: none;
  color: #fff;
}

#video-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  object-fit: cover;
  z-index: -1;
}

.content {
  padding-top: 1000px;
}

.content-wrapper {
  position: relative;
}

header {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  width: 100%;
  height: 100px;
  z-index: 1000;
  transition: background-color 0.3s ease-in-out, backdrop-filter 0.3s ease-in-out;
}

header span {
  padding: 20px;
  font-size: 36px;
  cursor: pointer;
}

header ul {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

header ul li {
  padding: 0 40px;
  font-size: 26px;
  cursor: pointer;
}

.menu-toggle {
  display: none;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 10px;
}

.menu-toggle svg {
  width: 30px;
  height: auto;
  fill: #fff;
}

.scrolled {
  background-color: #080808;
  backdrop-filter: none;
}

header.transition {
  transition: background-color 0.3s ease-in-out;
}

.aboutMe {
  position: relative;
  padding-top: 100px;
  color: white;
  background-color: black;
  opacity: 0.9;
}

.aboutMe h1,
.skills h1,
.projects h1,
.contact h1 {
  text-align: center;
  font-size: 45px;
  margin: 0 100px;
  padding: 10px;
  border-bottom: 2px solid white;
  font-weight: bold;
  color: #fff;
  padding: 20px;
}

.about-info {
  display: flex;
  justify-content: space-between;
  margin: 0 30px;
}

.personal-info {
  width: 45%;
  text-align: center;
}

.hobby-info {
  width: 45%;
  text-align: center;
}

.about-info h2 {
  font-size: 40px;
}

.about-info p {
  font-size: 20px;
}

.skills {
  color: #fff;
  text-align: center;
  margin-top: 50px;
}

.skills-img {
  padding-top: 20px;
  font-size: 120px;
  display: flex;
  justify-content: space-around;
}

.projects {
  margin-top: 200px;
  color: #fff;
  background-color: black;
  opacity: 0.9;
  margin-bottom: 150px;
}

.allProjects {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  text-align: center;
  padding: 40px;
}

.project-one,
.project-two,
.project-three,
.project-four,
.project-five {
  width: 15%;
  background-color: rgba(46, 45, 45, 0.6);
  padding: 20px;
}

.project-one h2,
.project-two h2,
.project-three h2,
.project-four h2,
.project-five h2 {
  font-size: 30px;
}

.project-one p,
.project-two p,
.project-three p,
.project-four p,
.project-five p {
  font-size: 16px;
}

.projects button {
  background-color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: 0.3s;
}

.projects button:hover {
  background-color: rgb(51, 51, 51);
  color: #fff;
  transition: 0.3s;
}

.contact {
  color: #fff;
}

.contact-info {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.contact-left h2 {
  font-size: 32px;
}

.icon {
  font-size: 80px;
  padding: 15px;
}

.personal-info span {
  display: block;
  text-align: start;
  font-size: 20px;
}

.contact p {
  text-align: center;
  font-size: 18px;
  font-weight: 100;
}






@media screen and (min-width:577px) and (max-width: 768px) {
  #video-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }

  .menu-toggle {
    display: block;
  }

  header {
    background-color: #080808;
  }

  header ul {
    display: none;
    position: absolute;
    top: 70px;
    right: 0;
    width: 100%;
    background-color: transparent;
    text-align: center;
    z-index: 999;
  }

  header ul.open {
    display: block;
    padding: 20px 0;
  }

  header ul.open li {
    display: block;
    padding: 10px;
    font-size: 18px;
    cursor: pointer;
    width: 100%;
    box-sizing: border-box;
    background-color: #080808;
    margin: -2px;
    text-align: start;
  }

  .about-info {
    display: block;
  }

  .personal-info,
  .hobby-info {
    width: 90%;
    margin: auto;
  }

  .personal-info h2,
  .hobby-info h2 {
    font-size: 22px;
  }

  .personal-info p,
  .hobby-info p {
    font-size: 18px;
    text-align: start;
  }

  .skills-img {
    font-size: 45px;
  }

  .allProjects {
    display: block;

  }

  .project-one,
  .project-two,
  .project-three,
  .project-four,
  .project-five {
    width: 90%;
    margin: auto;
    padding: 10px;
    margin-bottom: 20px;
  }

  .project-one p,
  .project-two p,
  .project-three p,
  .project-four p
  .project-five p {
    width: 70%;
    margin: auto;
    padding: 20px;
  }
}

